import { motion } from "framer-motion";
import React from "react";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";

function GalleryImageViewer({
  checkValidImage,
  GalleryImagesDataApi,
  icons,
  setselectedImage,
  SelectedValidImage,
  value,
  value2,
}) {
  function GalleryModalNext() {
    if (value2 < GalleryImagesDataApi.length - 1) {
      setselectedImage({
        ...value,
        value2: value2 + 1,
      });
    } else {
      setselectedImage({
        ...value,
        value2: 0,
      });
    }
  }

  function GalleryModalPrev() {
    const valueMinus = GalleryImagesDataApi.length - 1;
    if (value2 > 0) {
      setselectedImage({
        ...value,
        value2: value2 - 1,
      });
    } else {
      setselectedImage({
        ...value,
        value2: valueMinus,
      });
    }
  }

  return (
    <section className="group row-span-2 m-auto lg:mt-20 lg:mb-20 w-full lg:w-auto max-h-full h-full lg:h-3/5 relative md:z-10 lg:z-10 z-auto">
      <SelectedValidImage checkValidImage={checkValidImage} />
      <motion.div
        variants={icons}
        onClick={GalleryModalPrev}
        className="md:bg-yellow-300 text-4xl md:text-xl lg:text-xl md:hover:bg-yellow-400 lg:bg-yellow-300 lg:hover:bg-yellow-400 py-10 px-1 left-5 lg:left-20 fixed z-30 top-1/2 transform -translate-y-1/2 lg:transform lg:-translate-y-1/2 md:text-black lg:text-black text-white cursor-pointer"
      >
        <ChevronLeftIcon fontSize="inherit" />
      </motion.div>
      <motion.div
        variants={icons}
        onClick={GalleryModalNext}
        className="md:bg-yellow-300 text-4xl md:text-xl lg:text-xl md:hover:bg-yellow-400 lg:bg-yellow-300 lg:hover:bg-yellow-400 py-10 px-1 right-5 lg:right-20 fixed z-30 top-1/2 transform -translate-y-1/2 lg:transform lg:-translate-y-1/2 md:text-black lg:text-black text-white cursor-pointer"
      >
        <ChevronRightIcon fontSize="inherit" />
      </motion.div>
    </section>
  );
}

export default GalleryImageViewer;
