import React, { forwardRef, useEffect, useRef, useState } from "react";
import Slider from "react-slick";
import GalleryModal from "./Modal/GalleryModal";
import { AnimatePresence, motion } from "framer-motion";

import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import HelpIcon from "@material-ui/icons/Help";
import axios from "axios";
import { CircularProgress } from "@material-ui/core";

const Gallery = (props, ref) => {
  const [MapisOpen, setMapisOpen] = useState(false);
  const [selectedImage, setselectedImage] = useState();
  const [filterGallery, setfilterGallrey] = useState("Tattoos");

  const [GalleryImagesDataApi, setGalleryImagesDataApi] = useState([]);
  const [GalleryContentDataApi, setGalleryContentDataApi] = useState([]);
  const [GalleryEarPiercingDataApi, setGalleryEarPiercingDataApi] = useState(
    []
  );
  const [GalleryOralPiercingDataApi, setGalleryOralPiercingDataApi] = useState(
    []
  );
  const [GalleryFacialPiercingDataApi, setGalleryFacialPiercingDataApi] =
    useState([]);
  const [GalleryTorsoPiercingDataApi, setGalleryTorsoPiercingDataApi] =
    useState([]);
  const [GalleryGenitalPiercingDataApi, setGalleryGenitalPiercingDataApi] =
    useState([]);

  const [loading, setloading] = useState(false);

  const ApiUrl = `http://localhost/inkroots-dashboard/?rest_route=/wp/v2/gallery&per_page=20&_embed`;
  const ApiUrl2 = `http://localhost/inkroots-dashboard/?rest_route=/wp/v2/piercing&_embed`;
  const ApiUrl3 = `http://localhost/inkroots-dashboard/?rest_route=/wp/v2/ear_piercings&_embed`;
  const ApiUrl4 = `http://localhost/inkroots-dashboard/?rest_route=/wp/v2/oral_piercings&_embed`;
  const ApiUrl5 = `http://localhost/inkroots-dashboard/?rest_route=/wp/v2/facial_piercings&_embed`;
  const ApiUrl6 = `http://localhost/inkroots-dashboard/?rest_route=/wp/v2/torso_piercings&_embed`;
  const ApiUrl7 = `http://localhost/inkroots-dashboard/?rest_route=/wp/v2/genital_piercings&_embed`;

  useEffect(() => {
    axios.get(ApiUrl).then((res) => {
      const data = res.data;

      const dataMapped = data.map((params) => {
        const { _embedded, id, title, acf } = params;
        return { id, _embedded, title, acf };
      });

      setGalleryImagesDataApi(dataMapped);
    });

    if (filterGallery === "Piercing") {
      axios.get(ApiUrl2).then((res) => {
        const data = res.data;

        const dataMapped = data.map((params) => {
          const { id, title, acf, content } = params;
          return { id, title, acf, content };
        });

        setGalleryContentDataApi(dataMapped);
      });
      axios.get(ApiUrl3).then((res) => {
        const data = res.data;

        const dataMapped = data.map((params) => {
          const { id, title, acf, _embedded } = params;
          return { id, title, acf, _embedded };
        });

        setGalleryEarPiercingDataApi(dataMapped);
      });
      axios.get(ApiUrl4).then((res) => {
        const data = res.data;

        const dataMapped = data.map((params) => {
          const { id, title, acf, _embedded } = params;
          return { id, title, acf, _embedded };
        });

        setGalleryOralPiercingDataApi(dataMapped);
      });
      axios.get(ApiUrl5).then((res) => {
        const data = res.data;

        const dataMapped = data.map((params) => {
          const { id, title, acf, _embedded } = params;
          return { id, title, acf, _embedded };
        });

        setGalleryFacialPiercingDataApi(dataMapped);
      });
      axios.get(ApiUrl6).then((res) => {
        const data = res.data;

        const dataMapped = data.map((params) => {
          const { id, title, acf, _embedded } = params;
          return { id, title, acf, _embedded };
        });

        setGalleryTorsoPiercingDataApi(dataMapped);
      });
      axios.get(ApiUrl7).then((res) => {
        const data = res.data;

        const dataMapped = data.map((params) => {
          const { id, title, acf, _embedded } = params;
          return { id, title, acf, _embedded };
        });

        setGalleryGenitalPiercingDataApi(dataMapped);
      });
      setloading(true);
    }
  }, [
    ApiUrl,
    ApiUrl2,
    ApiUrl3,
    ApiUrl4,
    ApiUrl5,
    ApiUrl6,
    ApiUrl7,
    filterGallery,
  ]);

  function OpenGallery(e) {
    const id = Math.floor(e?.target.id);
    const dataset = Math.floor(e?.target.dataset.id);
    setMapisOpen((prev) => !prev);
    setselectedImage({
      value: dataset,
      value2: id,
    });
  }

  const GalleryImagesRef = useRef([]);
  GalleryImagesRef.current = [];

  const AddtoRefs = (el) => {
    if (el && !GalleryImagesRef.current.includes(el)) {
      GalleryImagesRef.current.push(el);
    }
  };

  function NextArrow(props) {
    const { style, onClick } = props;
    return (
      <div
        onClick={onClick}
        className="bg-yellow-300 hover:bg-yellow-500 py-10 px-1 absolute right-5 lg:right-52 top-1/2 text-black cursor-pointer z-10"
        style={{ transform: "translateY(-50%)", ...style }}
      >
        <ChevronRightIcon />
      </div>
    );
  }

  function PrevArrow(props) {
    const { style, onClick } = props;
    return (
      <div
        onClick={onClick}
        className="bg-yellow-300 hover:bg-yellow-500 py-10 px-1 absolute left-5 lg:left-52 top-1/2 text-black cursor-pointer z-10"
        style={{ transform: "translateY(-50%)", ...style }}
      >
        <ChevronLeftIcon />
      </div>
    );
  }

  function handleFilter(e) {
    const value = e.target.outerText;
    setfilterGallrey(value);
  }

  function isMobile() {
    const settings = {
      centerMode: true,
      infinite: true,
      autoplay: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      speed: 3000,
      centerPadding: "480px",
      nextArrow: <NextArrow />,
      prevArrow: <PrevArrow />,
    };
    const settings2 = {
      centerMode: true,
      infinite: true,
      autoplay: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      speed: 3000,
      centerPadding: "10px",
      nextArrow: <NextArrow />,
      prevArrow: <PrevArrow />,
    };
    if (window.innerWidth <= 768) {
      return settings2;
    }
    return settings;
  }

  // console.log(GalleryEarPiercingDataApi);

  const list = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        delayChildren: 0.2,
        staggerChildren: 0.3,
      },
    },
  };

  const item = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
    },
  };

  const tattoosFilter = (
    <motion.section
      className="grid grid-cols-2 lg:grid-cols-4"
      variants={list}
      initial="hidden"
      animate="visible"
    >
      {GalleryImagesDataApi.map(function (params, i) {
        const { _embedded, id, title, acf } = params;

        if (window.innerWidth <= 768) {
          return (
            <motion.figure
              variants={item}
              onClick={window.innerWidth <= 768 ? OpenGallery : null}
              data-id={i}
              id={i}
              className={`group col-span-${acf.mobile_columns} relative h-60 lg:h-72`}
              key={id}
            >
              <img
                id={id}
                ref={AddtoRefs}
                src={_embedded["wp:featuredmedia"][0]?.source_url}
                alt={title}
                className="absolute top-0 left-0 right-0 object-cover w-full h-full"
              />
              <div
                id={i}
                data-id={id}
                className="bg-black group-hover:opacity-50 opacity-25 absolute left-0 h-full w-full z-10"
              />
              <div className="grid w-full h-full absolute">
                <button
                  data-id={id}
                  data-index={i}
                  id={i}
                  onClick={OpenGallery}
                  className="group-hover:opacity-100 hidden lg:block lg:opacity-0 p-3 px-10 hover:bg-yellow-300 hover:text-black border-4 border-yellow-300 z-10 m-auto font-bold"
                >
                  View
                </button>
              </div>
            </motion.figure>
          );
        }
        return (
          <motion.figure
            variants={item}
            data-id={id}
            data-index={i}
            id={i}
            onClick={window.innerWidth <= 768 ? OpenGallery : null}
            className={`group col-span-${acf.columns} relative h-60 lg:h-72`}
            key={i}
          >
            <img
              id={id}
              ref={AddtoRefs}
              src={_embedded["wp:featuredmedia"][0]?.source_url}
              alt={title}
              className="absolute top-0 left-0 right-0 object-cover w-full h-full"
            />
            <div
              id={i}
              data-id={id}
              className="bg-black group-hover:opacity-50 opacity-25 absolute left-0 h-full w-full z-10"
            />
            <div className="grid w-full h-full absolute">
              <button
                data-id={id}
                data-index={i}
                id={i}
                onClick={OpenGallery}
                className="group-hover:opacity-100 hidden lg:block lg:opacity-0 p-3 px-10 hover:bg-yellow-300 hover:text-black border-4 border-yellow-300 z-10 m-auto font-bold"
              >
                View
              </button>
            </div>
          </motion.figure>
        );
      })}
    </motion.section>
  );

  const piercingsFilter = (
    <section className="grid">
      <div className="lg:ml-52 lg:mr-52 p-5">
        <h1 className="text-center p-4 pl-0 font-bold text-xl">
          {GalleryContentDataApi[0]?.title.rendered}
        </h1>
        <div
          dangerouslySetInnerHTML={{
            __html: GalleryContentDataApi[0]?.content.rendered,
          }}
        />
      </div>

      <div className=" w-full m-auto relative block overflow-hidden piercings">
        <h1 className="w-full text-center p-4 pl-0 font-bold text-xl">
          Ear Piercings
        </h1>
        <Slider {...isMobile()}>
          {GalleryEarPiercingDataApi.map((params, i) => {
            const { id, title, acf, _embedded } = params;
            return (
              <div key={id} className="w-60 relative slideThumb">
                <div
                  className="w-full p-6 absolute bottom-0"
                  style={{ backgroundColor: "rgba(0,0,0,.5" }}
                >
                  <h1 className="p-1 pl-0 pr-0 text-2xl font-bold">
                    {title.rendered}
                  </h1>
                  <h3 className="p-1 pl-0 pr-0 text-4xl lg:text-xl font-normal">
                    {acf.price}
                  </h3>
                  {acf.note ? (
                    <i className="p-1 pl-0 pr-0 text-xl lg:text-xs italic font-light">
                      {acf.note}
                    </i>
                  ) : null}
                </div>
                <img
                  className="w-full h-full object-cover"
                  src={_embedded["wp:featuredmedia"][0]?.source_url}
                  alt={title.rendered}
                />
              </div>
            );
          })}
        </Slider>
      </div>

      <div className="lg:ml-52 lg:mr-52 p-5">
        <div
          dangerouslySetInnerHTML={{
            __html: GalleryContentDataApi[0]?.acf.ear_piercing_section,
          }}
        />
      </div>
      <div className=" w-full m-auto relative block overflow-hidden piercings">
        <h1 className="w-full text-center p-4 pl-0 font-bold text-xl">
          Oral Piercings
        </h1>
        <Slider {...isMobile()}>
          {GalleryOralPiercingDataApi.map((params, i) => {
            const { id, title, acf, _embedded } = params;
            return (
              <div key={id} className="w-60 relative slideThumb">
                <div
                  className="w-full p-6 absolute bottom-0"
                  style={{ backgroundColor: "rgba(0,0,0,.5" }}
                >
                  <h1 className="p-1 pl-0 pr-0 text-2xl font-bold">
                    {title.rendered}
                  </h1>
                  <h3 className="p-1 pl-0 pr-0 text-4xl lg:text-xl font-normal">
                    {acf.price}
                  </h3>
                  {acf.note ? (
                    <i className="p-1 pl-0 pr-0 text-xl lg:text-xs italic font-light">
                      {acf.note}
                    </i>
                  ) : null}
                </div>
                <img
                  className="w-full h-full object-cover"
                  src={_embedded["wp:featuredmedia"][0]?.source_url}
                  alt={title.rendered}
                />
              </div>
            );
          })}
        </Slider>
      </div>
      <div className=" w-full m-auto relative block overflow-hidden piercings">
        <h1 className="w-full text-center p-4 pl-0 font-bold text-xl">
          Facial Piercings
        </h1>
        <Slider {...isMobile()}>
          {GalleryFacialPiercingDataApi.map((params, i) => {
            const { id, title, acf, _embedded } = params;
            return (
              <div key={id} className="w-60 relative slideThumb">
                <div
                  className="w-full p-6 absolute bottom-0"
                  style={{ backgroundColor: "rgba(0,0,0,.5" }}
                >
                  <h1 className="p-1 pl-0 pr-0 text-2xl font-bold">
                    {title.rendered}
                  </h1>
                  <h3 className="p-1 pl-0 pr-0 text-4xl lg:text-xl font-normal">
                    {acf.price}
                  </h3>
                  {acf.note ? (
                    <i className="p-1 pl-0 pr-0 text-xl lg:text-xs italic font-light">
                      {acf.note}
                    </i>
                  ) : null}
                </div>
                <img
                  className="w-full h-full object-cover"
                  src={_embedded["wp:featuredmedia"][0]?.source_url}
                  alt={title.rendered}
                />
              </div>
            );
          })}
        </Slider>
      </div>
      <div className="lg:ml-52 lg:mr-52 p-5">
        <div
          dangerouslySetInnerHTML={{
            __html: GalleryContentDataApi[0]?.acf.facial_piercing_section,
          }}
        />
      </div>
      <div className=" w-full m-auto relative block overflow-hidden piercings">
        <h1 className="w-full text-center p-4 pl-0 font-bold text-xl">
          Torso Piercings
        </h1>
        <Slider {...isMobile()}>
          {GalleryTorsoPiercingDataApi.map((params, i) => {
            const { id, title, acf, _embedded } = params;
            return (
              <div key={id} className="w-60 relative slideThumb">
                <div
                  className="w-full p-6 absolute bottom-0"
                  style={{ backgroundColor: "rgba(0,0,0,.5" }}
                >
                  <h1 className="p-1 pl-0 pr-0 text-2xl font-bold">
                    {title.rendered}
                  </h1>
                  <h3 className="p-1 pl-0 pr-0 text-4xl lg:text-xl font-normal">
                    {acf.price}
                  </h3>
                  {acf.note ? (
                    <i className="p-1 pl-0 pr-0 text-xl lg:text-xs italic font-light">
                      {acf.note}
                    </i>
                  ) : null}
                </div>
                <img
                  className="w-full h-full object-cover"
                  src={_embedded["wp:featuredmedia"][0]?.source_url}
                  alt={title.rendered}
                />
              </div>
            );
          })}
        </Slider>
      </div>
      <div className="lg:ml-52 lg:mr-52 p-5">
        <div
          dangerouslySetInnerHTML={{
            __html: GalleryContentDataApi[0]?.acf.torso_piercing_section,
          }}
        />
      </div>
      <div className=" w-full m-auto relative block overflow-hidden piercings">
        <h1 className="w-full text-center p-4 pl-0 font-bold text-xl">
          Genital Piercings
        </h1>
        <Slider {...isMobile()}>
          {GalleryGenitalPiercingDataApi.map((params, i) => {
            const { id, title, acf, _embedded } = params;
            return (
              <div key={id} className="w-60 relative slideThumb">
                <div
                  className="w-full p-6 absolute bottom-0"
                  style={{ backgroundColor: "rgba(0,0,0,.5" }}
                >
                  <h1 className="p-1 pl-0 pr-0 text-2xl font-bold">
                    {title.rendered}
                  </h1>
                  <h3 className="p-1 pl-0 pr-0 text-4xl lg:text-xl font-normal">
                    {acf.price}
                  </h3>
                  {acf.note ? (
                    <i className="p-1 pl-0 pr-0 text-xl lg:text-xs italic font-light">
                      {acf.note}
                    </i>
                  ) : null}
                </div>
                <img
                  className="w-full h-full object-cover"
                  src={_embedded["wp:featuredmedia"][0]?.source_url}
                  alt={title.rendered}
                />
              </div>
            );
          })}
        </Slider>
      </div>
      <div className="lg:ml-52 lg:mr-52 p-5">
        <h1 className="w-full text-center p-4 pl-0 font-bold text-xl text-red-600">
          {GalleryContentDataApi[0]?.acf.note_heading}{" "}
          <HelpIcon fontSize="large" />
        </h1>
        <div
          dangerouslySetInnerHTML={{
            __html: GalleryContentDataApi[0]?.acf.a_note,
          }}
        />
      </div>
    </section>
  );

  return (
    <main
      ref={ref}
      className="w-full h-full text-white relative bg-gradient-to-r from-black via-black to-black"
    >
      <div className="w-full flex-1 p-4" style={{ backgroundColor: "#1C1C1C" }}>
        <div className="mr-5 ml-5 lg:ml-52 lg:mr-52 block text-center space-x-5">
          <button
            onClick={handleFilter}
            className={`${
              filterGallery === "Tattoos" ? "bg-yellow-300 text-black" : ""
            } p-3 px-10 border-4 hover:bg-yellow-300 border-yellow-300 z-10 font-bold hover:text-black`}
          >
            Tattoos
          </button>
          <button
            onClick={handleFilter}
            className={`${
              filterGallery === "Piercing" ? "bg-yellow-300 text-black" : ""
            }  p-3 px-10 border-4 hover:bg-yellow-300 border-yellow-300 z-10 font-bold hover:text-black`}
          >
            Piercing
          </button>
        </div>
      </div>
      {filterGallery === "Tattoos" || filterGallery === "Piercings" ? (
        tattoosFilter
      ) : !loading ? (
        <div className="grid w-full h-full">
          <CircularProgress
            variant="indeterminate"
            className="text-yellow-300 text-center content-center m-auto"
            color="inherit"
            size={40}
            thickness={4}
          />
        </div>
      ) : (
        piercingsFilter
      )}
      <AnimatePresence>
        {MapisOpen ? (
          <GalleryModal
            selectedImage={selectedImage}
            OpenGallery={OpenGallery}
            GalleryImagesDataApi={GalleryImagesDataApi}
            setselectedImage={setselectedImage}
          />
        ) : null}
      </AnimatePresence>
    </main>
  );
};

export default forwardRef(Gallery);
