import React from "react";
import { useContext } from "react";
import CreateAppContext from "../../ContextProvider/CreateAppContext";
import ModalBg from "./ModalBG/ModalBg";

function MapModal({ OpenMap }) {
  const openModalBg = OpenMap;
  const { GoogleMap } = useContext(CreateAppContext);
  return (
    <ModalBg
      openModalBg={openModalBg}
      className={`w-full h-full fixed top-0 bottom-0 z-50 grid overflow-hidden`}
      style={{ backgroundColor: "rgba(0,0,0,.90)" }}
    >
      <section className="group row-span-2 mb-auto mt-0 md:m-auto lg:m-auto w-full lg:w-6/12 relative border-4 border-yellow-300">
        {GoogleMap}
      </section>
    </ModalBg>
  );
}

export default MapModal;
