import React, { useRef } from "react";
import CreateSectionRefsContext from "./CreateSectionRefsContext";

function SectionRefsContext({ children }) {
  const sectionRef = useRef([]);
  sectionRef.current = [];

  const AddtoRefs = (el) => {
    if (el && !sectionRef.current.includes(el)) {
      sectionRef.current.push(el);
    }
  };

  return (
    <CreateSectionRefsContext.Provider value={(AddtoRefs, sectionRef)}>
      {children}
    </CreateSectionRefsContext.Provider>
  );
}

export default SectionRefsContext;
