import React, { useEffect, useState } from "react";
import CreateAppContext from "./CreateAppContext";

export default function AppContext(props) {
  const [navPos, setnavPos] = useState(false);
  const [faqModalOpen, setfaqModalOpen] = useState(false);
  const [aftercareModalOpen, setaftercareModalOpen] = useState(false);
  const [searchModalOpen, setsearchModalOpen] = useState(false);
  const [socialModalOpen, setsocialModalOpen] = useState(false);
  const [SidePaneModalOpen, setSidePaneModalOpen] = useState(false);
  const [openMobileMenu, setopenMobileMenu] = useState(false);
  const [StoredSectionRefs, setStoredSectionRefs] = useState([]);
  const [StoredSocialIconsRefs, setStoredSocialIconsRefs] = useState([]);
  const [activeSocialMediaPage, setActiveSocialMediaPage] = useState("");
  const [newAppointment, setnewAppointment] = useState(false);
  const [GoogleMap, setGoogleMap] = useState();

  const [user, setUser] = useState();

  useEffect(() => {
    if (user === undefined || user === null) {
      const localStorageUser = localStorage.getItem("New User");
      setUser(JSON.parse(localStorageUser));
    }
  }, [user]);

  function openFAQModal() {
    setfaqModalOpen(!faqModalOpen);
  }

  function openAftercareModal() {
    setaftercareModalOpen(!aftercareModalOpen);
  }

  function openSearchModal() {
    setsearchModalOpen(!searchModalOpen);
  }

  function closeSocialModal() {
    setsocialModalOpen(false);
  }

  function openSidePane() {
    setSidePaneModalOpen(true);
  }

  function closeSidePane() {
    setSidePaneModalOpen(false);
  }

  function openMenuModal() {
    setopenMobileMenu(!openMobileMenu);
  }

  useEffect(() => {
    setGoogleMap(() => {
      return (
        <iframe
          title="inkroots map"
          src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d13263.169255136707!2d25.616202!3d-33.791861!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x58f5241c0831ba70!2sInk%20Roots%20Tattoo%20Studio!5e0!3m2!1sen!2sza!4v1631135764403!5m2!1sen!2sza"
          width="100%"
          height="550px"
          style={{ border: 0 }}
          allowfullscreen=""
          loading="lazy"
        ></iframe>
      );
    });
  }, []);

  return (
    <CreateAppContext.Provider
      value={{
        setnewAppointment,
        newAppointment,
        SidePaneModalOpen,
        closeSidePane,
        openSidePane,
        activeSocialMediaPage,
        closeSocialModal,
        socialModalOpen,
        searchModalOpen,
        openAftercareModal,
        openSearchModal,
        openMenuModal,
        faqModalOpen,
        openFAQModal,
        aftercareModalOpen,
        setopenMobileMenu,
        openMobileMenu,
        StoredSectionRefs,
        setStoredSectionRefs,
        setStoredSocialIconsRefs,
        setActiveSocialMediaPage,
        StoredSocialIconsRefs,
        setsocialModalOpen,
        setnavPos,
        navPos,
        user,
        GoogleMap,
      }}
    >
      {props.children}
    </CreateAppContext.Provider>
  );
}
