import React, { useContext } from "react";
import ModalBg from "./ModalBG/ModalBg";
import SearchIcon from "@material-ui/icons/Search";
import CreateAppContext from "../../ContextProvider/CreateAppContext";

function SearchModal() {
  const { openSearchModal } = useContext(CreateAppContext);
  const openModalBg = openSearchModal;
  return (
    <ModalBg
      openModalBg={openModalBg}
      className={`w-full h-full fixed top-0 bottom-0 z-50 grid`}
      style={{ backgroundColor: "rgba(0,0,0,.90)" }}
    >
      <div className="w-full md:w-8/12 lg:w-1/2 m-auto mt-0 pt-5 lg:p-5 text-white">
        <h1 className="text-yellow-300 w-full text-center p-4 pl-0 font-bold text-xl uppercase">
          Search INKROOTS TATTOOS
        </h1>
        <div>
          <div className=" m-auto text-yellow-300 text-left p-4 lg:pl-0 font-bold text-xl">
            <form className="w-full flex">
              <input
                className="w-full box-border py-3 px-3 my-2"
                placeholder="Search & Press enter"
              />
              <button className="py-3 px-3 bg-yellow-300 hover:bg-yellow-500 text-black my-2 h-full">
                <SearchIcon fontSize="large" className="" />
              </button>
            </form>
          </div>
        </div>
        <div className="overflow-y-auto w-full text-center mt-5 uppercase">
          Search Results Will Appear here
        </div>
      </div>
    </ModalBg>
  );
}

export default SearchModal;
