import "./App.css";
import About from "./Components/About";
import HomeSlider from "./Components/HomeSlider";
import Gallery from "./Components/Gallery";
import Contact from "./Components/Contact";
import { useEffect, useRef, useState } from "react";
import NavMenu from "./Components/NavMenu/NavMenu";
import MakeAnAppointment from "./Components/MakeAnAppointment";
// import SidePanel from "./Components/SidePanel/SidePanel";

function App() {
  const sectionRef = useRef([]);
  sectionRef.current = [];

  const [navHeight, setnavHeight] = useState();
  const [StoredSectionRefs, setStoredSectionRefs] = useState([]);
  const [activePage, setActivePage] = useState("");

  const AddtoRefs = (el) => {
    if (el && !sectionRef.current.includes(el)) {
      return sectionRef.current.push(el);
    }
  };

  useEffect(() => {
    setStoredSectionRefs(sectionRef.current);
  }, [sectionRef]);

  return (
    <>
      <NavMenu
        setActivePage={setActivePage}
        activePage={activePage}
        setnavHeight={setnavHeight}
        sectionRef={sectionRef}
      />
      <div className="flex h-screen">
        <div className="grid">
          <HomeSlider
            StoredSectionRefs={StoredSectionRefs}
            navHeight={navHeight}
            setActivePage={setActivePage}
            ref={AddtoRefs}
          />
          <About
            setActivePage={setActivePage}
            activePage={activePage}
            navHeight={navHeight}
            StoredSectionRefs={StoredSectionRefs}
            ref={AddtoRefs}
          />

          <MakeAnAppointment
            StoredSectionRefs={StoredSectionRefs}
            navHeight={navHeight}
            ref={AddtoRefs}
          />

          <Gallery StoredSectionRefs={StoredSectionRefs} ref={AddtoRefs} />

          <Contact ref={AddtoRefs} />
        </div>
      </div>
      {/* <SidePanel sectionRef={sectionRef} /> */}
    </>
  );
}

export default App;
