import React, { useContext } from "react";
import ModalBg from "./ModalBG/ModalBg";
import { List } from "@material-ui/core";
import CreateAppContext from "../../ContextProvider/CreateAppContext";

function NavDrawer({ MobileNavItemsMapped }) {
  const { openMenuModal } = useContext(CreateAppContext);
  const openModalBg = openMenuModal;

  const list = (
    <div
      className={`MobileNav w-full flex items-center text-white`}
      role="presentation"
    >
      <List>{MobileNavItemsMapped}</List>
    </div>
  );

  return (
    <ModalBg
      openModalBg={openModalBg}
      className="w-full h-full fixed top-0 bottom-0 z-10"
    >
      {list}
    </ModalBg>
  );
}

export default NavDrawer;
