import "date-fns";
import {
  CircularProgress,
  FormControl,
  FormHelperText,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  TextField,
} from "@material-ui/core";
import React, {
  forwardRef,
  useContext,
  useEffect,
  useRef,
  useState,
} from "react";
import HelpIcon from "@material-ui/icons/Help";
import AnnouncementIcon from "@material-ui/icons/Announcement";
import lottie from "lottie-web";
import axios from "axios";
import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from "@material-ui/pickers";
import CreateAppContext from "../ContextProvider/CreateAppContext";

const MakeAnAppointment = (props, ref) => {
  const { setnewAppointment } = useContext(CreateAppContext);
  const formSchema = {
    Any_other_question: "",
    Approximate_size: "",
    Brief_description_of_your_idea: "",
    Email: "",
    Name: "",
    Phone_Number: "",
    Date: {
      Pick_Date: "09/30/2021",
      Pick_Time: "09/30/2021",
      Time: "09/30/2021",
    },
    Specify_if_you_just_could_come_on_days_that_are_not_listed: "",
    Upload_your_pictures: "",
    Where_on_the_body_do_you_want_to_be_tattoed: "",
  };

  const { StoredSectionRefs, navHeight } = props;

  const [formStep, setformStep] = useState(0);
  const [formActiveStep, setFormActiveStep] = useState(0);

  const [formFields, setFormFields] = useState(formSchema);
  const [formFieldsSR, setFormFieldsSR] = useState();
  const [loading, setloading] = useState(false);
  const element = useRef(null);

  useEffect(() => {
    lottie.loadAnimation({
      container: element.current, // the dom element that will contain the animation
      renderer: "svg",
      loop: false,
      autoplay: true,
      animationData: require("../icons/27691-icons-success-done.json"),
      rendererSettings: {
        preserveAspectRatio: "xMinYMin slice",
      },
    });
  }, [loading]);

  function HandleFormStepChange(e) {
    e.preventDefault();
    // console.log(formFields);
    if (formFields.Any_other_question !== "") {
      if (formActiveStep === 2) {
        axios
          .post("/make-an-appointment", formFields)
          .then(setloading(true))
          .then((res) => {
            setloading(false);
            if (res.status === 200) {
              setformStep(formStep + 1);
              setFormActiveStep(formActiveStep + 1);
              setFormFieldsSR(res.data);
              localStorage.setItem("New User", JSON.stringify(res.data));
              setnewAppointment(true);
            }
          })
          .catch((err) => {
            console.log(err);
            alert(err, "Please try again later");
          });
      }
    }
    if (formActiveStep === 3) {
      setformStep(0);
      setFormActiveStep(0);
      setFormFields(formSchema);
      setFormFieldsSR();
    } else {
      setformStep(formStep + 1);
      setFormActiveStep(formActiveStep + 1);
      window.scrollTo(0, StoredSectionRefs[2]?.offsetTop - navHeight);
    }
  }

  function HandleFormStepChangePrev(e) {
    e.preventDefault();
    setformStep(formStep - 1);
    setFormActiveStep(formActiveStep - 1);
    window.scrollTo(0, StoredSectionRefs[2]?.offsetTop - navHeight);
  }

  function handleChangeInput(e) {
    setFormFields({ ...formFields, [e.target.name]: e.target.value });
  }

  const handleDateChange = (date) => {
    setFormFields({
      ...formFields,
      Date: {
        Pick_Date: date.toString().slice(0, 15),
        Pick_Time: date,
        Time: date.toString().slice(16, 21),
      },
    });
  };

  function handleClickInput(e) {
    const image = e.target.files[0];
    const reader = new FileReader();
    // const file = new File({ fileName: e.target.value }, { fileBits: image });

    reader.readAsDataURL(image);
    reader.onload = function (e) {
      setFormFields({
        ...formFields,
        Upload_your_pictures: e.target.result,
      });

      // localStorage.setItem(
      //   "New User Ref Image",
      //   JSON.stringify(e.target.result)
      // );
      // const ab = new Uint8Array(e.target.result);
      // console.log("formFields", ab);
    };
  }

  const formStep1 = (
    <>
      <TextField
        onChange={handleChangeInput}
        required
        require
        className="w-full box-border text-white rounded-full py-3 px-3"
        id="name"
        label="Name"
        name="Name"
        type="text"
        color="primary"
      />
      <TextField
        onChange={handleChangeInput}
        required
        className="w-full box-border text-white rounded-full py-3 px-3"
        id="phone number"
        label="Phone Number"
        name="Phone_Number"
        type="tel"
        color="primary"
      />
      <TextField
        onChange={handleChangeInput}
        required
        className="w-full box-border text-white rounded-full py-3 px-3"
        id="email"
        label="Email"
        name="Email"
        type="emial"
        color="primary"
      />
      <TextField
        onChange={handleChangeInput}
        required
        className="w-full box-border text-white rounded-full py-3 px-3"
        id="Brief description of your idea"
        label="Brief description of your idea"
        name="Brief_description_of_your_idea"
        type="text"
        color="primary"
        multiline
        rows={6}
      />
      <FormControl>
        <InputLabel id="demo-simple-select-helper-label">
          Placement of the tattoo
        </InputLabel>
        <Select
          labelId="Where on the body do you want to be tattoed"
          name="Where_on_the_body_do_you_want_to_be_tattoed"
          id="Where on the body do you want to be tattoed-helper"
          onChange={handleChangeInput}
          value={formFields.Where_on_the_body_do_you_want_to_be_tattoed}
        >
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          <MenuItem value={"Arms"}>Arms</MenuItem>
          <MenuItem value={"Legs"}>Legs</MenuItem>
          <MenuItem value={"Body"}>Body</MenuItem>
          <MenuItem value={"Face/Head"}>Face/Head</MenuItem>
        </Select>
        <FormHelperText>
          Where on the body do you want to be tattoed
        </FormHelperText>
      </FormControl>
      <FormControl>
        <TextField
          onChange={handleChangeInput}
          required
          className="w-full box-border text-white rounded-full py-3 px-3 font-bold"
          id="Approximate size"
          label="Approximate size"
          name="Approximate_size"
          type="text"
          color="primary"
        />
        <FormHelperText>*In cm</FormHelperText>
      </FormControl>
      <FormControl>
        <input
          label="File types: jpg,png"
          onChange={handleClickInput}
          className=" box-border border-0 text-black font-bold rounded-full py-3 px-3"
          id="Upload your pictures"
          placeholder="Upload your pictures"
          name="Upload_your_pictures"
          type="file"
          accept=".jpg,.jpeg,.png"
        />
        <FormHelperText>
          *You can upload your reference picture/s (Images of the part of the
          body to be tattoed.) Max size 5mb.
        </FormHelperText>
      </FormControl>
    </>
  );

  const formStep2 = (
    <>
      <h1 className="text-black w-full text-center p-4 pl-0 font-bold text-xl">
        Availability
      </h1>
      <p className="text-black">
        I'm based in Motherwell eKamvelihle, and the days I work are Monday to
        Saturday, 8am to 7pm. Although those are flexible depending on the
        circumnstances.
      </p>
      <i className="block text-red-500 mt-2 mb-2 font-bold italic">
        What dates are you planning to get your tattoo done?
      </i>
      {/* <TextField
        onChange={handleChangeInput}
        required
        id="Pick Date"
        label="Pick Date"
        type="date"
        defaultValue={currentDate}
      /> */}
      <MuiPickersUtilsProvider utils={DateFnsUtils}>
        <Grid container justifyContent="space-around">
          <KeyboardDatePicker
            className="w-full box-border text-white rounded-full py-3 px-3"
            margin="normal"
            id="date-picker-dialog"
            label="Date picker dialog"
            format="MM/dd/yyyy"
            value={formFields.Date?.Pick_Date}
            onChange={handleDateChange}
            KeyboardButtonProps={{
              "aria-label": "change date",
            }}
          />
          <KeyboardTimePicker
            className="w-full box-border text-white rounded-full py-3 px-3"
            margin="normal"
            id="time-picker"
            label="Time picker"
            value={formFields.Date?.Pick_Time}
            onChange={handleDateChange}
            KeyboardButtonProps={{
              "aria-label": "change time",
            }}
          />
        </Grid>
      </MuiPickersUtilsProvider>
      <FormControl>
        <InputLabel id="Specify if you just could come on days that are not listed">
          Other days
        </InputLabel>
        <Select
          labelId="Specify if you just could come on days that are not listed"
          id="Specify if you just could come on days that are not listed-helper"
          name="Specify_if_you_just_could_come_on_days_that_are_not_listed"
          onChange={handleChangeInput}
          value={
            formFields.Specify_if_you_just_could_come_on_days_that_are_not_listed
          }
        >
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          <MenuItem value={"Monday"}>Monday</MenuItem>
          <MenuItem value={"Tuesday"}>Tuesday</MenuItem>
          <MenuItem value={"Wednesday"}>Wednesday</MenuItem>
          <MenuItem value={"Thursday"}>Thursday</MenuItem>
          <MenuItem value={"Friday"}>Friday</MenuItem>
          <MenuItem value={"Saturday"}>Saturday</MenuItem>
        </Select>
        <FormHelperText>
          Specify if you just could come on days that are not listed.
        </FormHelperText>
      </FormControl>
    </>
  );

  const formStep3 = (
    <>
      <h1 className="text-black w-full text-center p-4 pl-0 font-bold text-xl">
        Almost Done
      </h1>
      <InputLabel id="demo-simple-select-helper-label">
        Any other question?
      </InputLabel>
      <FormControl>
        <TextField
          onChange={handleChangeInput}
          required
          name="Any_other_question"
          type="text"
          className="w-full box-border text-white rounded-full py-3 px-3"
          id="Any other question"
          color="primary"
          multiline
          rows={6}
        />
      </FormControl>
    </>
  );

  return (
    <main
      ref={ref}
      className="w-full text-white relative bg-yellow-300"
      style={{ height: "fit-content", display: "flex" }}
    >
      <div
        className={`bg-hero-pattern h-full opacity-30 w-full absolute bottom-0 z-0`}
      />
      <div
        className={`h-full w-full absolute top-0 bg-gradient-to-t from-transparent to-yellow-300 z-0`}
      />
      <div className="ml-5 mr-5 lg:ml-52 lg:mr-52 pt-5 pb-5 lg:p-5 relative">
        <h1 className="text-black w-full text-center p-4 pl-0 font-bold text-xl">
          <HelpIcon fontSize="large" /> Make an appointment
        </h1>

        <p className="text-black md:ml-20 md:mr-20">
          Please fill in the following appointment booking from. We will get
          back to you as soon as possible including a thorough desrciption of
          your project. All inquiries will be responded to in the order in which
          they are recieved. We are truly honored for every request that is
          submitted. For any additional questions please refer to our FAQ. THANK
          YOU
        </p>

        <div className="lg:ml-52 lg:mr-52 md:ml-20 md:mr-20 pt-5 pb-5 lg:p-5 lg:pl-0 lg:pr-0 relative">
          <form className="grid gap-2 z-10 " encType="multipart/form-data">
            {formActiveStep === 0 ? formStep1 : null}
            {formActiveStep === 1 ? formStep2 : null}
            {formActiveStep === 2 ? formStep3 : null}
            {loading ? (
              <div
                className="m-auto grid"
                style={{ width: "400px", height: "400px" }}
              >
                <CircularProgress
                  variant="indeterminate"
                  className="text-black text-center content-center m-auto"
                  color="inherit"
                  size={40}
                  thickness={4}
                />
              </div>
            ) : formActiveStep === 3 ? (
              <div
                className="m-auto"
                style={{ width: "auto", height: "auto" }}
                ref={element}
              >
                {formFieldsSR !== undefined ? (
                  <h1 className="w-full text-center font-bold text-black">
                    <AnnouncementIcon
                      fontSize="large"
                      className="text-green-500"
                    />{" "}
                    Your Appoinment has been placed
                  </h1>
                ) : (
                  <h1 className="w-full text-center font-bold text-red-600">
                    Please fill in all the required fields
                  </h1>
                )}

                <h1 className="w-full text-center font-bold text-2xl text-red-600">
                  {formFieldsSR?.Name}
                </h1>
              </div>
            ) : null}
            <div className="flex gap-4 m-auto mt-3">
              {formActiveStep ? (
                formActiveStep < 3 ? (
                  <button
                    onClick={HandleFormStepChangePrev}
                    className=" w-max rounded-full py-3 px-6 bg-white text-black hover:bg-yellow-300 font-bold mt-1 filter drop-shadow-lg"
                  >
                    Prev
                  </button>
                ) : null
              ) : null}
              <button
                onClick={HandleFormStepChange}
                className=" w-max rounded-full py-3 px-6 bg-white text-black hover:bg-yellow-300 font-bold mt-1 filter drop-shadow-lg"
              >
                {formActiveStep === 2 ? "Complete Appointment" : ""}
                {formActiveStep < 2 ? "Next" : ""}
                {formActiveStep === 3 ? "Finish" : ""}
              </button>
            </div>
          </form>
        </div>
      </div>
    </main>
  );
};

export default forwardRef(MakeAnAppointment);
