import React, { useContext } from "react";
import CloseIcon from "@material-ui/icons/Close";
import CreateAppContext from "../../ContextProvider/CreateAppContext";

function SocialPanel({ navRef }) {
  const { activeSocialMediaPage, closeSocialModal, socialModalOpen } =
    useContext(CreateAppContext);
  const NavHeight = navRef?.current?.clientHeight;

  return (
    <section
      className={`${
        socialModalOpen
          ? "z-30 opacity-100"
          : "select-none z-0 delay-150 opacity-0"
      } w-full h-screen fixed `}
      style={{ backgroundColor: "rgba(0, 0, 0, 0.65)" }}
    >
      <div
        className={`fixed h-full right-0 ${
          socialModalOpen ? "md:w-3/12 lg:w-2/6 p-5" : "md:w-0 lg:w-0 p-0"
        }  bg-yellow-300 overflow-hidden`}
        style={{ paddingTop: NavHeight }}
      >
        <div className="relative z-20 mt-10">{activeSocialMediaPage}</div>

        <div
          className={`h-full w-full fixed top-0 bg-gradient-to-t from-transparent via-yellow-300 to-yellow-300 z-10`}
        />
        <div
          className={`bg-hero-pattern h-full opacity-30 w-full fixed bottom-0`}
        />
      </div>
      <button
        onClick={closeSocialModal}
        className="hover:bg-red-700 bg-red-500 text-black block fixed right-10 bottom-10 p-5 rounded-full filter drop-shadow-lg"
      >
        <CloseIcon />
      </button>
    </section>
  );
}

export default SocialPanel;
