import React, { forwardRef, useEffect, useState } from "react";
import CloseIcon from "@material-ui/icons/Close";
import LocationOnIcon from "@material-ui/icons/LocationOn";
import PhoneIcon from "@material-ui/icons/Phone";
import WatchLaterIcon from "@material-ui/icons/WatchLater";
// import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";
import HelpIcon from "@material-ui/icons/Help";
import axios from "axios";

const About = (props, ref) => {
  const { StoredSectionRefs, navHeight, setActivePage } = props;
  const [ActivateAboutSelection, setActivateAboutSelection] = useState("");
  const [aboutFilter, setaboutFilter] = useState("Artist");

  function HandleClick(e) {
    setActivateAboutSelection(e.target?.innerText);
  }

  function HandleClose() {
    setActivateAboutSelection("");
  }

  function aboutArtist() {
    setaboutFilter("Artist");
  }

  function aboutInkroots() {
    setaboutFilter("Studio");
  }

  const [AboutMeDataApi, setAboutMeDataApi] = useState([]);

  const ActiveBodyConent = (AboutMeDataApi) => {
    if (aboutFilter === "Artist") {
      return AboutMeDataApi[1]?.content.rendered;
    } else if (aboutFilter === "Studio") {
      return AboutMeDataApi[0]?.content.rendered;
    }
  };

  const ApiUrl = `http://localhost/inkroots-dashboard/?rest_route=/wp/v2/about&_embed`;
  useEffect(() => {
    axios.get(ApiUrl).then((req) => {
      const data = req.data;

      const dataMapped = data.map((params) => {
        const { title, _embedded, id, content, acf } = params;
        return { id, title, content, acf, _embedded };
      });

      setAboutMeDataApi(dataMapped);
    });
  }, [ApiUrl]);

  const AboutMeDataMapped = function (AboutMeDataApi) {
    return (
      <div>
        <div className="relative">
          <main
            className="pt-10"
            dangerouslySetInnerHTML={{
              __html: ActiveBodyConent(AboutMeDataApi),
            }}
          />
        </div>
      </div>
    );
  };

  return (
    <main
      ref={ref}
      className="w-full text-white relative overflow-hidden bg-gradient-to-r from-black via-black to-black"
      style={{ height: "max-content" }}
    >
      <div
        className={`${
          ActivateAboutSelection ? "hidden" : ""
        } w-full flex flex-1 border-b-4 border-yellow-400`}
        style={{ backgroundColor: "#707070" }}
      >
        <div className="w-full ml-16 md:ml-10 md:mr-10 mr-16 lg:ml-52 lg:mr-52 pt-8 pb-8 flex flex-col md:flex-row lg:flex-row gap-5 lg:gap-0 place-content-between">
          <address className="flex not-italic items-center">
            <LocationOnIcon
              fontSize="large"
              className="text-black p-1 bg-yellow-300 hover:bg-black hover:text-white rounded-full mr-2 block cursor-pointer"
            />
            <div>
              <p className="p-1 pl-0 pr-0 w-full text-left flex-1">
                44 Jwarha Street
                <br />
                Motherwell, Gqeberha,6211
              </p>
            </div>
          </address>
          <address className="flex not-italic items-center">
            <PhoneIcon
              fontSize="large"
              className="text-black p-1 bg-yellow-300 hover:bg-black hover:text-white rounded-full mr-2 block cursor-pointer"
            />
            <div>
              <p className="p-1 pl-0 pr-0 w-full text-left flex-1">
                <a href="tel:+27735545565">+27 (0)73 554 5565</a>
              </p>
            </div>
          </address>
          <address className="flex not-italic items-center">
            <WatchLaterIcon
              fontSize="large"
              className="text-black p-1 bg-yellow-300 hover:bg-black hover:text-white rounded-full mr-2 block cursor-pointer"
            />
            <div>
              <p className="p-1 pl-0 pr-0 w-full text-left flex-1">
                7:30 am - 10:00 pm
                <br />
                Monday - Saturday
              </p>
            </div>
          </address>
        </div>
      </div>
      <section className={`flex flex-col lg:flex-row min-h-screen lg:h-screen`}>
        <div
          className={`group ${
            ActivateAboutSelection ? "w-full lg:w-4/5" : ""
          }  bg-gray-100 h-full relative border-r-4 border-yellow-400 about_button flex flex-1`}
        >
          <img
            src={
              AboutMeDataApi[1]?._embedded["wp:featuredmedia"][0]?.source_url
            }
            alt=""
            className="absolute z-0 left-0 h-full w-full object-cover"
          />
          <div className="bg-black group-hover:opacity-75 opacity-50 absolute left-0 h-full w-full z-10" />
          {ActivateAboutSelection ? (
            <div className="flex m-auto">
              <span
                onClick={() => aboutArtist()}
                className={`p-3 px-10 ${
                  aboutFilter === "Artist" ? "bg-yellow-300 text-black" : ""
                } hover:bg-yellow-400 hover:text-black z-10 m-auto text-2xl cursor-pointer`}
              >
                {AboutMeDataApi[1]?.acf.about}
              </span>
              <span
                onClick={() => aboutInkroots()}
                className={`p-3 px-10 ${
                  aboutFilter === "Studio" ? "bg-yellow-300 text-black" : ""
                } hover:bg-yellow-400 hover:text-black hover:border-yellow-400 z-10 m-auto text-2xl cursor-pointer`}
              >
                {AboutMeDataApi[0]?.acf.about}
              </span>
            </div>
          ) : (
            <span
              onClick={HandleClick}
              className={`lg:opacity-0 opacity-100 group-hover:opacity-100 p-3 px-10 hover:bg-yellow-300 hover:text-black border-4 border-yellow-300 z-10 m-auto cursor-pointer`}
            >
              About Me
            </span>
          )}
          {ActivateAboutSelection ? (
            <CloseIcon
              onClick={HandleClose}
              fontSize="large"
              className="bg-yellow-400 absolute right-5 -bottom-16 lg:-right-9  lg:top-1/2  lg:transform  lg:-translate-y-1/2 rounded-full lg:rounded-l-none lg:rounded-r-full z-30 lg:z-20 p-2 cursor-pointer text-black"
            />
          ) : null}
        </div>

        <section
          className={
            ActivateAboutSelection
              ? "lg:w-3/5 w-full h-full relative"
              : "w-0 h-0 flex relative"
          }
        >
          <div
            className={`${
              ActivateAboutSelection ? "opacity-100" : "opacity-0"
            } m-auto w-4/5 pt-20 pb-10 z-20 relative`}
          >
            <h1 className="header-text text-center text-4xl">
              {aboutFilter === "Artist"
                ? AboutMeDataApi[1]?.title.rendered
                : AboutMeDataApi[0]?.title.rendered}
            </h1>
            {AboutMeDataMapped(AboutMeDataApi)}
            {aboutFilter === "Studio" ? (
              <div
                className={`${
                  ActivateAboutSelection ? "opacity-100" : "opacity-0"
                } p-5 pb-0 lg:pb-5 flex items-center lg:m-10 mt-5 lg:mb-0 bg-yellow-300`}
              >
                <HelpIcon className="text-green-600" fontSize="large" />
                <h1 className="p-2 text-black font-bold">
                  {AboutMeDataApi[0].acf.important_note}
                </h1>
              </div>
            ) : null}
          </div>
          <div
            className={`h-full w-full absolute top-0 bg-gradient-to-t from-transparent via-black to-black z-10`}
          />
          <div
            className={`bg-hero-pattern h-full opacity-30 w-full absolute bottom-0`}
          />
        </section>

        <div
          className={`${
            ActivateAboutSelection ? "hidden" : ""
          } h-full flex-1 grid relative`}
          style={{ backgroundColor: "#707070", alignContent: "center" }}
        >
          <img
            src={
              AboutMeDataApi[0]?._embedded["wp:featuredmedia"][0]?.source_url
            }
            alt=""
            className="z-0 p-5 lg:p-0 m-auto w-3/4"
          />
        </div>

        <div
          className={`${
            ActivateAboutSelection ? "hidden" : ""
          } group bg-gray-100 h-full relative border-l-4 border-yellow-400 about_button flex flex-1`}
        >
          <img
            src={AboutMeDataApi[1]?.acf.my_work}
            alt=""
            className="absolute z-0 left-0 h-full w-full object-cover"
          />
          <div className="bg-black group-hover:opacity-75 opacity-50 absolute left-0 h-full w-full z-10" />

          {ActivateAboutSelection ? null : (
            <span
              onClick={() => {
                window.scrollTo(0, StoredSectionRefs[3].offsetTop - navHeight);
                localStorage.setItem("Nav Item", "Gallery");
                setActivePage(localStorage.getItem("Nav Item"));
              }}
              className={`lg:opacity-0 opacity-100 group-hover:opacity-100 p-3 px-10 hover:bg-yellow-300 hover:text-black border-4 border-yellow-300 z-10 m-auto cursor-pointer`}
            >
              See My Work
            </span>
          )}
        </div>
      </section>
    </main>
  );
};

export default forwardRef(About);
