import React from "react";
import ReactDOM from "react-dom";
import CloseIcon from "@material-ui/icons/Close";

function ModalBg({ children, openModalBg }) {
  return ReactDOM.createPortal(
    <div
      className={`w-full h-full fixed top-0 bottom-0 z-50 grid`}
      style={{ backgroundColor: "rgba(0,0,0,.90)" }}
    >
      {children}
      <button
        onClick={openModalBg}
        className="bg-yellow-300 hover:bg-yellow-400 text-black block fixed right-10 bottom-10 p-5 rounded-full z-50"
      >
        <CloseIcon />
      </button>
    </div>,
    document.getElementById("modal")
  );
}

export default ModalBg;
