import React, { forwardRef, useEffect, useState } from "react";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";

import axios from "axios";

import "slick-carousel/slick/slick.css";
import Slider from "react-slick";
import { AnimatePresence, motion } from "framer-motion";

const HomeSlider = (props, ref) => {
  const [SliderDataApi, setSliderDataApi] = useState([]);
  const { StoredSectionRefs, navHeight, setActivePage } = props;

  function isMobile() {
    const settings = {
      dots: true,
      infinite: true,
      autoplay: true,
      arrows: true,
      speed: 2000,
      slidesToShow: 1,
      slidesToScroll: 1,
      vertical: true,
      verticalSwiping: true,
      nextArrow: <NextArrow />,
      prevArrow: <PrevArrow />,
      rtl: false,
    };
    const settings2 = {
      dots: false,
      infinite: true,
      autoplay: true,
      arrows: true,
      speed: 2000,
      slidesToShow: 1,
      slidesToScroll: 1,
      vertical: true,
      verticalSwiping: true,
      nextArrow: <NextArrow />,
      prevArrow: <PrevArrow />,
      rtl: false,
    };
    if (window.innerWidth <= 768) {
      return settings2;
    }
    return settings;
  }

  function NextArrow(props) {
    const { style, onClick } = props;
    return (
      <div
        className={`absolute left-10 lg:left-56 bottom-20 ml-4 z-10`}
        style={{ ...style }}
        onClick={onClick}
      >
        <span className="rounded-full bg-yellow-400 p-2 ml-4 hover:bg-yellow-200 cursor-pointer">
          <ArrowForwardIcon fontSize="medium" style={{ color: "black" }} />
        </span>
      </div>
    );
  }

  function PrevArrow(props) {
    const { style, onClick } = props;
    return (
      <div
        className={`absolute left-0 lg:left-48 bottom-20 ml-4 z-10`}
        style={{ ...style }}
        onClick={onClick}
      >
        <span className="rounded-full bg-yellow-400 p-2 hover:bg-yellow-200 cursor-pointer">
          <ArrowBackIcon fontSize="medium" style={{ color: "black" }} />
        </span>
      </div>
    );
  }

  const ApiUrl = `http://localhost/inkroots-dashboard/?rest_route=/wp/v2/main-slider&_embed`;

  useEffect(() => {
    axios.get(ApiUrl).then((req) => {
      const data = req.data;

      const dataMapped = data.map((params) => {
        const { title, _embedded, id, acf } = params;
        return { id, title, acf, _embedded };
      });

      setSliderDataApi(dataMapped);
    });
  }, [ApiUrl, setSliderDataApi]);

  const SliderContent = SliderDataApi.map(function (params) {
    const { id, title, _embedded, acf } = params;
    return (
      <AnimatePresence key={id}>
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          className="slider h-screen"
        >
          <div className="absolute h-screen w-full lg:w-1/2 right-0 top-0 bottom-0">
            <img
              className="filter object-cover w-full h-full grayscale"
              src={_embedded["wp:featuredmedia"][0]?.source_url}
              alt=""
            />
          </div>

          <div className="Slider_Title absolute left-5 lg:left-52 top-2/4 z-30">
            <h1 className="lg:text-8xl md:text-7xl text-4xl header-text mb-3">
              <span className="block md:text-2xl lg:text-base text-base font-sans pb-4 font-bold">
                {acf.sub_heading}
              </span>
              {title.rendered}
            </h1>

            <span
              onClick={() => {
                window.scrollTo(0, StoredSectionRefs[2].offsetTop - navHeight);
                localStorage.setItem("Nav Item", "Make an appointment");
                setActivePage(localStorage.getItem("Nav Item"));
              }}
              className="rounded-full py-3 px-6 bg-yellow-400 hover:bg-yellow-200 cursor-pointer text-black font-bold"
            >
              Book Now
            </span>
          </div>
          <div
            className={`h-full w-full lg:hidden absolute top-0 bg-gradient-to-t from-black to-transparent z-0`}
          />
        </motion.div>
      </AnimatePresence>
    );
  });

  return (
    <main
      ref={ref}
      className="w-full h-screen text-white overflow-hidden relative bg-gradient-to-r from-black via-black to-black"
    >
      <Slider {...isMobile()}>{SliderContent.reverse()}</Slider>
    </main>
  );
};

export default forwardRef(HomeSlider);
