import React, { forwardRef, useState } from "react";
import ContactForm from "./Contact Components/ContactForm";

import StorefrontIcon from "@material-ui/icons/Storefront";
import MailOutlineIcon from "@material-ui/icons/MailOutline";
import PhoneIcon from "@material-ui/icons/Phone";
import WhatsAppIcon from "@material-ui/icons/WhatsApp";
import InstagramIcon from "@material-ui/icons/Instagram";
import FacebookIcon from "@material-ui/icons/Facebook";

import MapModal from "./Modal/MapModal";
import { useContext } from "react";
import CreateAppContext from "../ContextProvider/CreateAppContext";

const Contact = (props, ref) => {
  const [MapisOpen, setMapisOpen] = useState(false);
  const { GoogleMap } = useContext(CreateAppContext);

  function OpenMap() {
    setMapisOpen(!MapisOpen);
  }

  return (
    <main
      ref={ref}
      className="w-full h-full text-white relative bg-gradient-to-r from-black via-black to-black"
    >
      <section className="flex flex-col-reverse lg:flex-row h-full">
        <ContactForm />
        <div className={`h-full flex-1`} style={{ backgroundColor: "#1c1c1c" }}>
          <div className="flex flex-col">
            <section className="h-full p-5">
              <h1 className="header-text text-3xl text-white w-full text-center">
                Contact us
              </h1>
              <div className="h-full flex flex-col md:flex-row lg:flex-row lg:p-5">
                <address className="m-auto mt-0 mb-0 w-full lg:w-4/12 text-center">
                  <StorefrontIcon
                    fontSize="large"
                    className="bg-yellow-300 rounded-full p-1 m-4 text-black"
                  />
                  <p>44 Jwarha Street Motherwell, Gqeberha,6211</p>
                  <p className="p-1 pl-0 pr-0 pt-5 lg:pt-0 w-full text-center lg:text-left">
                    <PhoneIcon
                      fontSize="medium"
                      className="text-yellow-400 hover:text-yellow-200 mr-2 block cursor-pointer"
                    />
                    +27 (0)73 554 5565
                  </p>
                  <p className="p-1 pl-0 pr-0 w-full text-center lg:text-left">
                    <WhatsAppIcon
                      fontSize="medium"
                      className="text-yellow-400 hover:text-yellow-200 mr-2 block cursor-pointer"
                    />
                    +27 (0)73 554 5565
                  </p>
                  <p className="p-1 pl-0 pr-0 w-full text-center lg:text-left">
                    <InstagramIcon
                      fontSize="medium"
                      className="text-yellow-400 hover:text-yellow-200 mr-2 block cursor-pointer"
                    />
                    inkroots_tattoos
                  </p>
                  <p className="p-1 pl-0 pr-0 w-full text-center lg:text-left">
                    <FacebookIcon
                      fontSize="medium"
                      className="text-yellow-400 hover:text-yellow-200 mr-2 block cursor-pointer "
                    />
                    inkroots_tattoo
                  </p>
                </address>
                <address className="m-auto mt-0 mb-0 w-full lg:w-4/12 text-center">
                  <MailOutlineIcon
                    fontSize="large"
                    className="bg-yellow-300 rounded-full p-1 m-4 text-black"
                  />
                  <p>
                    <a href="mailto:test@gmail.com">
                      inkrootstattoos@inkroots-tattoos.co.za
                    </a>
                  </p>
                </address>
              </div>
            </section>

            <section className="group bg-gray-200 w-full relative">
              <div className="bg-black group-hover:opacity-50 opacity-50 absolute left-0 h-full w-full z-10" />
              <div className="grid w-full h-full absolute">
                <button
                  onClick={OpenMap}
                  className="group-hover:opacity-100 opacity-0 p-3 px-10 hover:bg-yellow-300 hover:text-black border-4 border-yellow-300 z-10 m-auto font-bold"
                >
                  View Map
                </button>
              </div>
              {GoogleMap}
            </section>
          </div>
        </div>
      </section>
      {MapisOpen ? <MapModal OpenMap={OpenMap} /> : null}
    </main>
  );
};

export default forwardRef(Contact);
