import React, { useContext, useEffect, useRef } from "react";
import AftercareModal from "../Modal/AftercareModal";
import SearchIcon from "@material-ui/icons/Search";
import PersonIcon from "@material-ui/icons/Person";
import WhatsAppIcon from "@material-ui/icons/WhatsApp";
import TwitterIcon from "@material-ui/icons/Twitter";
import InstagramIcon from "@material-ui/icons/Instagram";
import LinkedInIcon from "@material-ui/icons/LinkedIn";
import FacebookIcon from "@material-ui/icons/Facebook";
import MenuIcon from "@material-ui/icons/Menu";
import FaqModal from "../Modal/FaqModal";
import SearchModal from "../Modal/SearchModal";
import SocialPanel from "../Modal/SocialPanel";
import NavDrawer from "../Modal/NavDrawer";
import { ListItem } from "@material-ui/core";
// import SidePane from "../Track Appointment/SidePane";
import TrackAppointment from "../Track Appointment/TrackAppointment";
import CreateAppContext from "../../ContextProvider/CreateAppContext";

function NavMenu(props) {
  const {
    setnewAppointment,
    newAppointment,
    openSidePane,
    socialModalOpen,
    searchModalOpen,
    openAftercareModal,
    openSearchModal,
    openMenuModal,
    faqModalOpen,
    openFAQModal,
    aftercareModalOpen,
    setopenMobileMenu,
    openMobileMenu,
    StoredSectionRefs,
    setStoredSectionRefs,
    setStoredSocialIconsRefs,
    setActiveSocialMediaPage,
    StoredSocialIconsRefs,
    setsocialModalOpen,
    setnavPos,
    navPos,
  } = useContext(CreateAppContext);

  const { sectionRef, setnavHeight, activePage, setActivePage } = props;

  const NavItems = [
    {
      id: 0,
      title: "Home",
      link: "/",
    },
    {
      id: 1,
      title: "About",
      link: "/about",
    },
    {
      id: 2,
      title: "Make an appointment",
      link: "/make an appointment",
    },
    {
      id: 3,
      title: "Gallery",
      link: "/gallery",
    },
    {
      id: 4,
      title: "Contact",
      link: "/contact",
    },
    {
      id: 5,
      title: "Aftercare",
      link: "/aftercare",
    },
    {
      id: 6,
      title: "FAQ",
      link: "/faq",
    },
  ];

  const SocialMediaInfo = [
    {
      id: 0,
      icon: (
        <WhatsAppIcon
          fontSize="medium"
          className={`${
            navPos || socialModalOpen
              ? "text-black hover:text-white"
              : " text-white hover:text-black"
          } w-full h-full`}
        />
      ),
      name: "WhatsApp",
    },
    {
      id: 1,
      icon: (
        <TwitterIcon
          fontSize="medium"
          className={`${
            navPos || socialModalOpen
              ? "text-black hover:text-white"
              : " text-white hover:text-black"
          } w-full h-full`}
        />
      ),
      name: "Twitter",
    },
    {
      id: 2,
      icon: (
        <LinkedInIcon
          fontSize="medium"
          className={`${
            navPos || socialModalOpen
              ? "text-black hover:text-white"
              : " text-white hover:text-black"
          } w-full h-full`}
        />
      ),
      name: "LinkedIn",
    },
    {
      id: 3,
      icon: (
        <InstagramIcon
          fontSize="medium"
          className={`${
            navPos || socialModalOpen
              ? "text-black hover:text-white"
              : " text-white hover:text-black"
          } w-full h-full`}
        />
      ),
      name: "Instagram",
    },
    {
      id: 4,
      icon: (
        <FacebookIcon
          fontSize="medium"
          className={`${
            navPos || socialModalOpen
              ? "text-black hover:text-white"
              : " text-white hover:text-black"
          } w-full h-full`}
        />
      ),
      name: "Facebook",
    },
  ];

  const navRef = useRef();
  const SocialIconsRef = useRef();
  SocialIconsRef.current = [];

  const executeScroll = (id) => {
    const idNumber = id.target.id;
    const innerText = id.target.outerText;
    window.scrollTo(
      0,
      StoredSectionRefs[idNumber]?.offsetTop - navRef?.current?.clientHeight
    );
    setActivePage(innerText);
    if (activePage !== "") {
      localStorage.setItem("Nav Item", innerText);
    }
  };

  useEffect(() => {
    setStoredSectionRefs(sectionRef.current);
    setStoredSocialIconsRefs(SocialIconsRef.current);
  }, [
    sectionRef,
    SocialIconsRef,
    setStoredSectionRefs,
    setStoredSocialIconsRefs,
  ]);

  const AddtoSocialIconsRef = (el) => {
    if (el && !SocialIconsRef.current.includes(el)) {
      return SocialIconsRef.current.push(el);
    }
  };

  function openSocialModal({ id }) {
    setActiveSocialMediaPage(StoredSocialIconsRefs[id].id);
    setsocialModalOpen(true);
  }

  const NavItemsMapped = NavItems.map(function (params, i) {
    const { title, id } = params;
    if (id === 6) {
      return (
        <li
          onClick={openFAQModal}
          className={`${
            navPos ? "bg-red-500" : ""
          } flex-auto p-2 font-bold relative group hover:font-light cursor-pointer`}
          key={i}
          id={id}
        >
          {title}
          <span
            className={`${navPos ? "bg-gray-900" : "bg-yellow-300"} ${
              activePage === title ? "opacity-100 bg-gray-900" : ""
            } h-1 w-full absolute opacity-0 group-hover:opacity-100 -bottom-2 left-0 m-auto`}
          />
        </li>
      );
    } else if (id === 5) {
      return (
        <li
          onClick={openAftercareModal}
          className={`${
            navPos ? "bg-black" : ""
          } flex-auto p-2 font-bold relative group hover:font-light cursor-pointer`}
          key={i}
          id={id}
        >
          {title}
          <span
            className={`${navPos ? "bg-gray-900" : "bg-yellow-300"} ${
              activePage === title ? "opacity-100 bg-gray-900" : ""
            } h-1 w-full absolute opacity-0 group-hover:opacity-100 -bottom-2 left-0 m-auto`}
          />
        </li>
      );
    } else {
      return (
        <li
          onClick={(id) => {
            executeScroll(id);
          }}
          className={`${
            navPos ? "text-black" : ""
          } flex-auto p-2 font-bold relative group hover:font-light cursor-pointer`}
          key={i}
          id={id}
        >
          {title}
          <span
            className={`${navPos ? "bg-gray-900" : "bg-yellow-300"} ${
              activePage === title ? "opacity-100 bg-gray-900" : ""
            } h-1 w-full absolute opacity-0 group-hover:opacity-100 -bottom-2 left-0 m-auto`}
          />
        </li>
      );
    }
  });

  const SocialMediaInfoMapped = SocialMediaInfo.map(function (params, i) {
    const { icon, name, id } = params;
    return (
      <span
        ref={AddtoSocialIconsRef}
        onClick={(name) => openSocialModal({ name, id })}
        key={i}
        id={name}
        className="w-6 h-6 cursor-pointer"
      >
        {icon}
        <span className="hidden">{name}</span>
      </span>
    );
  });

  window.onload = () => {
    window.onscroll = () => {
      if (window.scrollY > StoredSectionRefs[0]?.offsetTop) {
        setnavPos(true);
        setnavHeight(navRef?.current?.clientHeight);
      } else if (window.scrollY === 0) {
        setnavPos(false);
      }
    };

    document.onscroll = () => {
      if (
        window.scrollY >=
        StoredSectionRefs[4]?.offsetTop - navRef?.current?.clientHeight
      ) {
        localStorage.setItem("Nav Item", "Contact");
        setActivePage("Contact");
      } else if (
        window.scrollY >=
        StoredSectionRefs[3]?.offsetTop - navRef?.current?.clientHeight
      ) {
        localStorage.setItem("Nav Item", "Gallery");
        setActivePage("Gallery");
      } else if (
        window.scrollY >=
        StoredSectionRefs[2]?.offsetTop - navRef?.current?.clientHeight
      ) {
        localStorage.setItem("Nav Item", "Make an appointment");
        setActivePage("Make an appointment");
      } else if (
        window.scrollY >=
        StoredSectionRefs[1]?.offsetTop - navRef?.current?.clientHeight
      ) {
        localStorage.setItem("Nav Item", "About");
        setActivePage("About");
      } else if (
        window.scrollY >=
        StoredSectionRefs[0]?.offsetTop - navRef?.current?.clientHeight
      ) {
        localStorage.setItem("Nav Item", "Home");
        setActivePage("Home");
      }
    };

    if (activePage === "") {
      setActivePage(localStorage.getItem("Nav Item"));
    }
  };

  const MobileNavItemsMapped = NavItems.map(function (params, i) {
    const { title, id } = params;
    if (id === 6) {
      return (
        <ListItem
          key={i}
          button
          onClick={() => {
            openFAQModal();
            setopenMobileMenu(!openMobileMenu);
          }}
        >
          <li
            id={id}
            className={`flex-auto p-2 font-bold relative group hover:font-light cursor-pointer bg-red-500`}
          >
            {title}
            <span
              className={`${navPos ? "" : "bg-yellow-300"} ${
                activePage === title
                  ? "opacity-100 bg-yellow-300 lg:bg-gray-900"
                  : ""
              } h-1 w-full absolute opacity-0 group-hover:opacity-100 -bottom-2 left-0 m-auto`}
            />
          </li>
        </ListItem>
      );
    } else if (id === 5) {
      return (
        <ListItem
          key={i}
          button
          onClick={() => {
            openAftercareModal();
            setopenMobileMenu(!openMobileMenu);
          }}
        >
          <li
            id={id}
            className={`${
              navPos ? "bg-black" : ""
            } flex-auto p-2 font-bold relative group hover:font-light cursor-pointer text-black bg-yellow-300`}
          >
            {title}
            <span
              className={`${navPos ? "" : "bg-yellow-300"} ${
                activePage === title
                  ? "opacity-100 bg-yellow-300 lg:bg-gray-900"
                  : ""
              } h-1 w-full absolute opacity-0 group-hover:opacity-100 -bottom-2 left-0 m-auto`}
            />
          </li>
        </ListItem>
      );
    } else {
      return (
        <ListItem
          key={i}
          button
          onClick={(id) => {
            executeScroll(id);
            setopenMobileMenu(!openMobileMenu);
          }}
        >
          <li
            id={id}
            className={`${
              navPos ? "text-black" : ""
            } flex-auto p-2 font-bold relative group text-white hover:font-light cursor-pointer`}
          >
            {title}
            <span
              className={`${navPos ? "" : "bg-yellow-300"} ${
                activePage === title
                  ? "opacity-100 bg-yellow-300 lg:bg-gray-900"
                  : ""
              } h-1 w-full absolute opacity-0 group-hover:opacity-100 -bottom-2 left-0 m-auto`}
            />
          </li>
        </ListItem>
      );
    }
  });

  useEffect(() => {
    const getLocalStorage = localStorage.getItem("New User");
    // console.log(getLocalStorage);
    if (getLocalStorage !== null) {
      setnewAppointment(true);
    } else {
      setnewAppointment(false);
    }
  }, [setnewAppointment]);

  return (
    <>
      <header
        ref={navRef}
        className={`${
          navPos ? "lg:bg-yellow-300 bg-black filter drop-shadow-lg" : ""
        } w-full pl-0 pr-0 fixed top-0 left-0 right-0 text-white z-40 overflow-hidden lg:bg-transparent`}
      >
        <nav className="flex relative">
          <div
            className={`${
              navPos ? "bg-black" : ""
            } lg:ml-48 w-40 lg:w-52 md:w-60 p-3`}
          >
            <img
              onClick={() => {
                window.scroll(0, 0);
                localStorage.setItem("Nav Item", "Home");
                setActivePage(localStorage.getItem("Nav Item"));
              }}
              src="./imgs/logo_inkroots.png"
              alt="inkroots logo"
              className="cursor-pointer h-full w-full"
            />
          </div>
          <ul className="lg:flex gap-2 hidden ml-4 items-center">
            {NavItemsMapped}
          </ul>
          <div className="social_media hidden m-auto lg:flex gap-2 ">
            {SocialMediaInfoMapped}
          </div>
          <div className=" absolute z-10 top-1/2 transform -translate-y-2/4 right-16 lg:right-10">
            <div className="flex gap-2">
              <SearchIcon
                onClick={openSearchModal}
                fontSize="large"
                className={`${
                  navPos || socialModalOpen
                    ? "bg-black border-white text-white lg:text-white hover:bg-white hover:text-black"
                    : "bg-yellow-300 border-black lg:border-yellow-300 lg:hover:border-white text-black hover:bg-black hover:text-white"
                } p-1 w-14 h-14 border-2 rounded-full cursor-pointer`}
              />
              {newAppointment ? (
                <PersonIcon
                  title="I'm a tooltip"
                  onClick={openSidePane}
                  fontSize="large"
                  className={`${
                    navPos || socialModalOpen
                      ? "bg-black border-white text-white lg:text-white hover:bg-white hover:text-black"
                      : "bg-yellow-300 border-black lg:border-yellow-300 lg:hover:border-white text-black hover:bg-black hover:text-white"
                  } p-1 w-14 h-14 border-2 rounded-full cursor-pointer`}
                />
              ) : null}
            </div>
          </div>

          <MenuIcon
            onClick={openMenuModal}
            fontSize="large"
            className={`${
              navPos || socialModalOpen
                ? "bg-black border-white text-white lg:text-white hover:bg-white hover:text-black"
                : "bg-yellow-300 lg:text-white border-black lg:border-white text-black hover:bg-black hover:text-white"
            } absolute z-0 top-1/2 lg:hidden lg:opacity-0 transform -translate-y-2/4 lg:right-10 right-5 p-1 w-14 h-14 border-2 rounded-full cursor-pointer`}
          />
        </nav>
      </header>
      {openMobileMenu ? (
        <NavDrawer MobileNavItemsMapped={MobileNavItemsMapped} />
      ) : null}
      {faqModalOpen ? <FaqModal /> : null}
      {aftercareModalOpen ? <AftercareModal /> : null}
      {searchModalOpen ? <SearchModal /> : null}
      <SocialPanel navRef={navRef} />
      {newAppointment ? <TrackAppointment navRef={navRef} /> : null}
    </>
  );
}

export default NavMenu;
