// import React, { useEffect, useState } from "react";ChevronRight
import ReactDOM from "react-dom";
import CloseIcon from "@material-ui/icons/Close";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import AspectRatioIcon from "@material-ui/icons/AspectRatio";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ExpandLessIcon from "@material-ui/icons/ExpandLess";
import { motion } from "framer-motion";
import { useState } from "react";
import GalleryImageViewer from "../GalleryImageViewer";

function GalleryModal(props) {
  const { OpenGallery, selectedImage, setselectedImage, GalleryImagesDataApi } =
    props;

  const [aspectRatio, setaspectRatio] = useState(false);
  const [collectionisOpen, setcollectionisOpen] = useState(false);

  const checkValidImage = () => {
    if (GalleryImagesDataApi[selectedImage.value2]) {
      return GalleryImagesDataApi[selectedImage.value2]?._embedded[
        "wp:featuredmedia"
      ][0]?.source_url;
    }
  };
  // console.log(selectedImage);

  function SelectedValidImage({ checkValidImage }) {
    return (
      <motion.img
        initial="hidden"
        animate="visible"
        variants={ImageScale}
        exit={{ opacity: 0.5 }}
        src={checkValidImage()}
        alt=""
        className={`lg:h-full lg:w-auto w-full h-full ${
          aspectRatio ? "object-contain" : "object-cover"
        } lg:object-contain drop-shadow-2xl md:border-4 lg:border-4 md:border-yellow-300 lg:border-yellow-300`}
      />
    );
  }

  const value = selectedImage.value;
  const value2 = selectedImage.value2;

  function ActiveSelectedImage(e) {
    const id = Math.floor(e?.target.id);
    setselectedImage({
      ...value,
      value2: id,
    });
  }

  const variants = {
    visible: {
      scale: 1,
      opacity: 1,
      transform: {
        type: "spring",
        delayChildren: 0.5,
        staggerChildren: 0.2,
      },
    },
    hidden: {
      scale: 0.8,
      opacity: 0.8,
    },
  };

  const ImageScale = {
    visible: {
      scale: 1,
      opacity: 1,
    },
    hidden: {
      opacity: 0.2,
      scale: 0.8,
    },
  };

  const item = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
    },
  };

  const item2 = {
    hidden: { opacity: 0.6 },
    visible: {
      opacity: 1,
    },
  };

  const icons = {
    hidden: { x: -20, opacity: 0 },
    visible: {
      x: 0,
      opacity: 1,
    },
  };
  const icons2 = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
    },
  };

  const thumbNailSlide = GalleryImagesDataApi.map(function (params, i) {
    const { _embedded, title, id } = params;
    if (selectedImage.value2 === i) {
      return (
        <div
          key={id}
          className="w-full h-20 lg:h-auto contents lg:block relative"
        >
          <motion.img
            variants={item}
            src={_embedded["wp:featuredmedia"][0]?.source_url}
            alt={title.rendered}
            className="w-full h-20 lg:h-20 border-4 border-yellow-300 cursor-pointer object-cover box-border"
          />
          <motion.span
            variants={item}
            className="h-1 w-full bg-yellow-300 absolute -bottom-3 left-0 m-auto"
          />
        </div>
      );
    } else {
      return (
        <div
          key={id}
          className="lg:w-full w-auto h-20 lg:h-auto contents lg:block relative"
        >
          <motion.img
            variants={item2}
            key={id}
            id={i}
            onClick={ActiveSelectedImage}
            src={_embedded["wp:featuredmedia"][0]?.source_url}
            alt={title.rendered}
            className="w-full h-20 lg:h-20 object-cover filter grayscale hover:grayscale-100 hover:opacity-75 cursor-pointer"
          />
        </div>
      );
    }
  });

  function ChangeAspectRatio() {
    setaspectRatio(!aspectRatio);
  }

  function OpenCollection() {
    setcollectionisOpen(!collectionisOpen);
  }

  return ReactDOM.createPortal(
    <motion.div
      initial="hidden"
      animate="visible"
      exit="hidden"
      variants={variants}
      className={`w-full h-full fixed top-0 bottom-0 z-50 flex flex-col overflow-hidden`}
      style={{ backgroundColor: "rgba(0,0,0,.75)" }}
    >
      <img
        src={checkValidImage()}
        alt=""
        className="w-full h-full hidden md:block lg:block transform scale-110 object-cover absolute top-0 bottom-0 z-0 filter blur-lg"
      />
      <GalleryImageViewer
        checkValidImage={checkValidImage}
        GalleryImagesDataApi={GalleryImagesDataApi}
        setselectedImage={setselectedImage}
        icons={icons2}
        SelectedValidImage={SelectedValidImage}
        value={value}
        value2={value2}
      />

      <button
        onClick={OpenGallery}
        className="bg-yellow-300 hidden hover:bg-yellow-500 text-black fixed right-10 top-10 z-20 lg:bottom-auto lg:top-10 p-5 rounded-full filter drop-shadow-2xl"
      >
        <CloseIcon />
      </button>
      <motion.button
        variants={icons}
        onClick={OpenGallery}
        className="text-white block fixed left-2 top-2 z-20 lg:bottom-auto lg:top-10 p-5 rounded-full"
      >
        <ArrowBackIcon fontSize="large" color="inherit" />
      </motion.button>
      <motion.button
        variants={icons}
        onClick={ChangeAspectRatio}
        className={`${
          aspectRatio ? "text-red-500" : "text-white"
        }  block md:hidden lg:hidden fixed right-2 top-2 p-5 z-20 lg:bottom-auto lg:top-10 rounded-full`}
      >
        <AspectRatioIcon fontSize="large" color="inherit" />
      </motion.button>
      <button
        onClick={OpenCollection}
        className={`text-white  ${
          collectionisOpen ? "opacity-85 bottom-32" : "opacity-50 bottom-1"
        } block md:hidden lg:hidden fixed left-1/2 transform -translate-x-1/2 z-20 lg:bottom-auto lg:top-10 p-5 rounded-full`}
      >
        {collectionisOpen ? (
          <>
            <span className="block">Close</span>
            <ExpandLessIcon fontSize="large" color="inherit" />
          </>
        ) : (
          <>
            <span className="block">Gallery</span>
            <ExpandMoreIcon fontSize="large" color="inherit" />
          </>
        )}
      </button>
      {collectionisOpen ? (
        <div
          className={`fixed w-full thumbnail left-0 right-0 lg:left-auto lg:right-auto bottom-10 lg:bottom-24 lg:grid grid-flow-col flex gap-4 h-auto lg:h-auto overflow-x-auto lg:overflow-visible lg:m-auto mt-0 mb-0 z-10`}
        >
          {thumbNailSlide}
        </div>
      ) : null}
      <div
        className={`fixed w-full thumbnail hidden left-0 right-0 lg:left-auto lg:right-auto bottom-10 lg:bottom-24 lg:grid grid-flow-col gap-4 h-auto lg:h-auto overflow-x-auto lg:overflow-visible lg:m-auto mt-0 mb-0 z-10`}
      >
        {thumbNailSlide}
      </div>

      <div
        className={`w-full ${
          collectionisOpen ? "h-full opacity-85" : "h-3/5 opacity-50"
        } md:opacity-100 lg:opacity-100 absolute bottom-0 left-0 right-0 z-0 bg-gradient-to-t from-black to-transparent`}
      />
      <div
        className={`w-full h-32 ${
          aspectRatio ? "opacity-0" : "opacity-70"
        }  absolute top-0 left-0 right-0 bg-gradient-to-t from-transparent to-black select-none`}
      />
    </motion.div>,
    document.getElementById("modal")
  );
}

export default GalleryModal;
