import React, { useContext, useEffect, useState } from "react";
import HelpIcon from "@material-ui/icons/Help";
import ModalBg from "./ModalBG/ModalBg";
import axios from "axios";
import { CircularProgress } from "@material-ui/core";
import CreateAppContext from "../../ContextProvider/CreateAppContext";

function FaqModal() {
  const { openFAQModal } = useContext(CreateAppContext);
  const openModalBg = openFAQModal;

  const [FaqDataApi, setFaqDataApi] = useState([]);
  const [loading, setloading] = useState(false);

  const ApiUrl = `http://localhost/inkroots-dashboard/?rest_route=/wp/v2/pages&_embed`;

  useEffect(() => {
    axios.get(ApiUrl).then((res) => {
      const data = res.data;

      const dataMapped = data.map((params) => {
        const { id, title, content } = params;
        return { id, title, content };
      });

      setFaqDataApi(dataMapped);
      setloading(true);
    });
  }, [ApiUrl]);

  return (
    <ModalBg
      openModalBg={openModalBg}
      className={`w-full h-full fixed top-0 bottom-0 z-50 grid`}
      style={{ backgroundColor: "rgba(0,0,0,.90)" }}
    >
      {!loading ? (
        <CircularProgress
          variant="indeterminate"
          className="text-yellow-300 text-center content-center m-auto"
          color="inherit"
          size={40}
          thickness={4}
        />
      ) : (
        <div className="lg:ml-52 lg:mr-52 pb-20 p-5 lg:p-5 text-white overflow-y-auto">
          <h1 className="text-yellow-300 w-full text-center p-4 pl-0 font-bold text-xl">
            {FaqDataApi[1]?.title.rendered} <HelpIcon fontSize="large" />
          </h1>

          <div
            dangerouslySetInnerHTML={{
              __html: FaqDataApi[1]?.content.rendered,
            }}
          />
        </div>
      )}
    </ModalBg>
  );
}

export default FaqModal;
